import { defineStore } from 'pinia'

export const useHeaderStore = defineStore('header', {
   state: () => ({
      isHeaderMenuVisible: false,
      isUserMenuVisible: false,
      isModalVisible: false,
      isLogVisible: false,
      isRegVisible: false,
   }),
   actions: {
      hideWindow(): void {
         this.$patch({ isModalVisible: false, isRegVisible: false, isLogVisible: false });
         this.$router.push(this.modalQueryString);
      }
   },
   getters: {
      modalQueryString(state) {
         if (state.isLogVisible) {
            return { path: '', query: { login: 'autorize' } }
         }
         else if (state.isRegVisible) {
            return { path: '', query: { registration: 'new' } }
         }
         else {
            return { path: '' };
         }
      }
   }
});
